.footer {
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  border-top: 1px solid rgba(0, 212, 255, 0.2);
  position: relative;
}

.footer::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 0%, rgba(0, 212, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.footer-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.footer-content {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  gap: 3rem;
  padding: 4rem 0 2rem;
}

.footer-section {
  display: flex;
  flex-direction: column;
}

.footer-logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.footer-logo-img {
  width: 40px;
  height: 40px;
  object-fit: contain;
  filter: drop-shadow(0 0 10px rgba(0, 212, 255, 0.5));
}

.footer-logo-text {
  font-family: 'Orbitron', monospace;
  font-weight: 700;
  font-size: 1.5rem;
  letter-spacing: 2px;
}

.footer-description {
  font-size: 1rem;
  line-height: 1.6;
  color: #ccc;
  max-width: 400px;
}

.footer-title {
  font-family: 'Orbitron', monospace;
  font-size: 1.3rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: #00d4ff;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.footer-links {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-links li {
  margin-bottom: 0.8rem;
}

.footer-link {
  color: #ccc;
  text-decoration: none;
  font-size: 1rem;
  transition: all 0.3s ease;
  position: relative;
  padding-left: 0;
}

.footer-link::before {
  content: '>';
  color: #00d4ff;
  margin-right: 0.5rem;
  opacity: 0;
  transition: all 0.3s ease;
}

.footer-link:hover {
  color: #00d4ff;
  padding-left: 1rem;
}

.footer-link:hover::before {
  opacity: 1;
}

.footer-social {
  display: flex;
  gap: 1rem;
}

.footer-social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(0, 212, 255, 0.1);
  border: 2px solid rgba(0, 212, 255, 0.3);
  color: #00d4ff;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

.footer-social-link:hover {
  background: rgba(0, 212, 255, 0.2);
  border-color: #00d4ff;
  box-shadow: 0 0 15px rgba(0, 212, 255, 0.4);
  color: #fff;
}

.footer-bottom {
  padding: 2rem 0;
}

.footer-line {
  width: 100%;
  height: 1px;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.3), transparent);
  margin-bottom: 2rem;
}

.footer-bottom-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.footer-copyright {
  font-size: 0.9rem;
  color: #ccc;
  margin: 0;
}

.footer-made-with {
  font-size: 0.9rem;
  color: #ccc;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.heart-icon {
  color: #ff6b6b;
  animation: heartbeat 2s ease-in-out infinite;
}

@keyframes heartbeat {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

@media (max-width: 768px) {
  .footer-content {
    grid-template-columns: 1fr;
    gap: 2rem;
    padding: 3rem 0 2rem;
  }

  .footer-section {
    text-align: center;
  }

  .footer-logo {
    justify-content: center;
  }

  .footer-description {
    max-width: 100%;
  }

  .footer-social {
    justify-content: center;
  }

  .footer-bottom-content {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }

  .footer-copyright,
  .footer-made-with {
    font-size: 0.8rem;
  }
}

@media (max-width: 480px) {
  .footer-container {
    padding: 0 1rem;
  }

  .footer-content {
    padding: 2rem 0 1rem;
  }

  .footer-logo-img {
    width: 35px;
    height: 35px;
  }

  .footer-logo-text {
    font-size: 1.3rem;
  }

  .footer-description {
    font-size: 0.9rem;
  }

  .footer-title {
    font-size: 1.2rem;
  }

  .footer-link {
    font-size: 0.9rem;
  }

  .footer-social-link {
    width: 45px;
    height: 45px;
    font-size: 1.1rem;
  }

  .footer-copyright,
  .footer-made-with {
    font-size: 0.75rem;
  }
} 