.skills {
  padding: 6rem 0;
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  position: relative;
}

.skills::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 20%, rgba(0, 212, 255, 0.03) 0%, transparent 50%),
              radial-gradient(circle at 80% 80%, rgba(0, 212, 255, 0.03) 0%, transparent 50%);
  pointer-events: none;
}

.skills-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.skills-intro {
  text-align: center;
  margin-bottom: 4rem;
}

.skills-description {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #ccc;
  max-width: 800px;
  margin: 0 auto;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.skill-item {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  padding: 2rem;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.skill-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.05), transparent);
  transition: left 0.5s;
}

.skill-item:hover::before {
  left: 100%;
}

.skill-item:hover {
  border-color: rgba(0, 212, 255, 0.4);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.1);
}

.skill-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.skill-icon {
  font-size: 2.5rem;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.skill-info {
  flex: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.skill-name {
  font-family: 'Rajdhani', sans-serif;
  font-size: 1.3rem;
  font-weight: 600;
  color: #fff;
  margin: 0;
}

.skill-level {
  font-family: 'Orbitron', monospace;
  font-size: 1.1rem;
  font-weight: 700;
  color: #00d4ff;
  text-shadow: 0 0 10px rgba(0, 212, 255, 0.5);
}

.skill-progress {
  width: 100%;
  height: 8px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  position: relative;
}

.skill-progress::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  animation: shimmer 2s infinite;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.skill-progress-bar {
  height: 100%;
  border-radius: 4px;
  position: relative;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  transition: width 1s ease;
}

.skills-summary {
  margin-bottom: 4rem;
}

.summary-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
}

.summary-item {
  text-align: center;
  padding: 2rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.summary-item:hover {
  background: rgba(0, 212, 255, 0.1);
  border-color: rgba(0, 212, 255, 0.4);
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.2);
}

.summary-number {
  font-family: 'Orbitron', monospace;
  font-size: 3rem;
  font-weight: 900;
  color: #00d4ff;
  margin-bottom: 0.5rem;
  text-shadow: 0 0 15px rgba(0, 212, 255, 0.5);
}

.summary-label {
  font-size: 1.1rem;
  color: #fff;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.skills-cta {
  text-align: center;
  padding: 3rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 20px;
  backdrop-filter: blur(10px);
}

.cta-text {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #fff;
  max-width: 800px;
  margin: 0 auto;
}

@media (max-width: 768px) {
  .skills {
    padding: 4rem 0;
  }

  .skills-description {
    font-size: 1.1rem;
  }

  .skills-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .skill-item {
    padding: 1.5rem;
  }

  .skill-icon {
    font-size: 2rem;
    width: 50px;
    height: 50px;
  }

  .skill-name {
    font-size: 1.2rem;
  }

  .skill-level {
    font-size: 1rem;
  }

  .summary-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .summary-item {
    padding: 1.5rem;
  }

  .summary-number {
    font-size: 2.5rem;
  }

  .skills-cta {
    padding: 2rem;
  }

  .cta-text {
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .skill-header {
    flex-direction: column;
    text-align: center;
    gap: 0.5rem;
  }

  .skill-info {
    flex-direction: column;
    gap: 0.5rem;
  }

  .skill-icon {
    font-size: 1.8rem;
    width: 45px;
    height: 45px;
  }

  .skill-name {
    font-size: 1.1rem;
  }

  .summary-number {
    font-size: 2rem;
  }

  .skills-cta {
    padding: 1.5rem;
  }

  .cta-text {
    font-size: 1rem;
  }
} 