.contact {
  padding: 6rem 0;
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  position: relative;
}

.contact::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 20% 80%, rgba(0, 212, 255, 0.05) 0%, transparent 50%),
              radial-gradient(circle at 80% 20%, rgba(0, 212, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.contact-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.contact-intro {
  text-align: center;
  margin-bottom: 4rem;
}

.contact-description {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #ccc;
  max-width: 800px;
  margin: 0 auto;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem;
  margin-bottom: 4rem;
}

.contact-info {
  padding-right: 2rem;
}

.contact-subtitle {
  font-family: 'Orbitron', monospace;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: #00d4ff;
}

.contact-items {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.contact-item {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 1.5rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
}

.contact-item:hover {
  border-color: rgba(0, 212, 255, 0.4);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.1);
}

.contact-icon {
  font-size: 2rem;
  color: #00d4ff;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 212, 255, 0.1);
  border-radius: 12px;
  border: 1px solid rgba(0, 212, 255, 0.3);
}

.contact-details {
  flex: 1;
}

.contact-title {
  font-family: 'Rajdhani', sans-serif;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: #fff;
}

.contact-value {
  font-size: 1rem;
  color: #ccc;
  text-decoration: none;
  transition: color 0.3s ease;
}

.contact-value:hover {
  color: #00d4ff;
}

.social-section {
  padding-left: 2rem;
}

.social-subtitle {
  font-family: 'Orbitron', monospace;
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: #00d4ff;
}

.social-description {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #ccc;
  margin-bottom: 2rem;
}

.social-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 1.5rem;
}

.social-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  padding: 2rem;
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  backdrop-filter: blur(10px);
  text-decoration: none;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.social-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.05), transparent);
  transition: left 0.5s;
}

.social-card:hover::before {
  left: 100%;
}

.social-card:hover {
  border-color: rgba(0, 212, 255, 0.4);
  box-shadow: 0 10px 30px rgba(0, 212, 255, 0.2);
  transform: translateY(-5px);
}

.social-icon {
  font-size: 2.5rem;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.social-name {
  font-family: 'Rajdhani', sans-serif;
  font-size: 1.1rem;
  font-weight: 600;
  color: #fff;
  text-align: center;
}

.contact-cta {
  text-align: center;
  padding: 4rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 20px;
  backdrop-filter: blur(10px);
}

.cta-content {
  max-width: 800px;
  margin: 0 auto;
}

.cta-title {
  font-family: 'Orbitron', monospace;
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: #00d4ff;
}

.cta-text {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #fff;
  margin-bottom: 2rem;
}

.cta-buttons {
  display: flex;
  gap: 1.5rem;
  justify-content: center;
  flex-wrap: wrap;
}

.cta-button {
  display: inline-block;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 1rem 2rem;
  border-radius: 50px;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.cta-button.primary {
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  color: #000;
  border: none;
}

.cta-button.secondary {
  background: transparent;
  color: #00d4ff;
  border: 2px solid #00d4ff;
}

.cta-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.cta-button:hover::before {
  left: 100%;
}

.cta-button:hover {
  text-decoration: none;
}

.cta-button.primary:hover {
  color: #000;
}

.cta-button.secondary:hover {
  color: #fff;
  background: rgba(0, 212, 255, 0.1);
}

@media (max-width: 768px) {
  .contact {
    padding: 4rem 0;
  }

  .contact-description {
    font-size: 1.1rem;
  }

  .contact-content {
    grid-template-columns: 1fr;
    gap: 3rem;
  }

  .contact-info {
    padding-right: 0;
  }

  .contact-subtitle {
    font-size: 1.8rem;
  }

  .contact-item {
    padding: 1rem;
  }

  .contact-icon {
    font-size: 1.5rem;
    width: 50px;
    height: 50px;
  }

  .social-section {
    padding-left: 0;
  }

  .social-subtitle {
    font-size: 1.8rem;
  }

  .social-description {
    font-size: 1rem;
  }

  .social-grid {
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 1rem;
  }

  .social-card {
    padding: 1.5rem;
  }

  .social-icon {
    font-size: 2rem;
    width: 50px;
    height: 50px;
  }

  .contact-cta {
    padding: 2rem;
  }

  .cta-title {
    font-size: 2rem;
  }

  .cta-text {
    font-size: 1.1rem;
  }

  .cta-buttons {
    flex-direction: column;
    align-items: center;
  }

  .cta-button {
    width: 100%;
    max-width: 300px;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .contact-subtitle {
    font-size: 1.5rem;
  }

  .contact-item {
    flex-direction: column;
    text-align: center;
    gap: 1rem;
  }

  .contact-icon {
    font-size: 1.3rem;
    width: 45px;
    height: 45px;
  }

  .social-subtitle {
    font-size: 1.5rem;
  }

  .social-grid {
    grid-template-columns: 1fr;
  }

  .social-card {
    padding: 1rem;
  }

  .social-icon {
    font-size: 1.8rem;
    width: 45px;
    height: 45px;
  }

  .contact-cta {
    padding: 1.5rem;
  }

  .cta-title {
    font-size: 1.8rem;
  }

  .cta-text {
    font-size: 1rem;
  }

  .cta-button {
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
  }
} 