.projects {
  padding: 6rem 0;
  background: linear-gradient(135deg, #000 0%, #0a0a0a 50%, #000 100%);
  position: relative;
}

.projects::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 30% 70%, rgba(0, 212, 255, 0.05) 0%, transparent 50%),
              radial-gradient(circle at 70% 30%, rgba(0, 212, 255, 0.05) 0%, transparent 50%);
  pointer-events: none;
}

.projects-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  position: relative;
  z-index: 1;
}

.projects-intro {
  text-align: center;
  margin-bottom: 4rem;
}

.projects-description {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #ccc;
  max-width: 800px;
  margin: 0 auto;
}

.projects-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.project-card {
  background: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 15px;
  overflow: hidden;
  backdrop-filter: blur(10px);
  transition: all 0.3s ease;
  position: relative;
}

.project-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(0, 212, 255, 0.05), transparent);
  transition: left 0.5s;
  z-index: 1;
}

.project-card:hover::before {
  left: 100%;
}

.project-card:hover {
  border-color: rgba(0, 212, 255, 0.4);
  box-shadow: 0 15px 40px rgba(0, 212, 255, 0.2);
}

.project-image {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.project-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, rgba(0, 212, 255, 0.1), rgba(0, 153, 204, 0.1));
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.project-icon {
  font-size: 4rem;
  color: #00d4ff;
  text-shadow: 0 0 20px rgba(0, 212, 255, 0.5);
  z-index: 2;
}

.project-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 3;
}

.project-card:hover .project-overlay {
  opacity: 1;
}

.project-links {
  display: flex;
  gap: 1rem;
}

.project-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: rgba(0, 212, 255, 0.2);
  border: 2px solid rgba(0, 212, 255, 0.5);
  color: #00d4ff;
  font-size: 1.2rem;
  text-decoration: none;
  transition: all 0.3s ease;
}

.project-link:hover {
  background: rgba(0, 212, 255, 0.3);
  border-color: #00d4ff;
  box-shadow: 0 0 20px rgba(0, 212, 255, 0.5);
  color: #fff;
}

.project-content {
  padding: 2rem;
  position: relative;
  z-index: 2;
}

.project-title {
  font-family: 'Orbitron', monospace;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  color: #00d4ff;
}

.project-description {
  font-size: 1rem;
  line-height: 1.6;
  color: #ccc;
  margin-bottom: 1.5rem;
}

.project-technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.technology-tag {
  background: rgba(0, 212, 255, 0.1);
  border: 1px solid rgba(0, 212, 255, 0.3);
  color: #00d4ff;
  padding: 0.3rem 0.8rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
  transition: all 0.3s ease;
}

.technology-tag:hover {
  background: rgba(0, 212, 255, 0.2);
  border-color: rgba(0, 212, 255, 0.5);
  transform: translateY(-2px);
}

.projects-cta {
  text-align: center;
  padding: 3rem;
  background: rgba(0, 212, 255, 0.05);
  border: 1px solid rgba(0, 212, 255, 0.2);
  border-radius: 20px;
  backdrop-filter: blur(10px);
}

.cta-text {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #fff;
  max-width: 800px;
  margin: 0 auto 2rem;
}

.github-button {
  display: inline-block;
  background: linear-gradient(45deg, #00d4ff, #0099cc);
  border: none;
  color: #000;
  font-family: 'Rajdhani', sans-serif;
  font-weight: 600;
  font-size: 1.1rem;
  padding: 1rem 2rem;
  border-radius: 50px;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.github-button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.5s;
}

.github-button:hover::before {
  left: 100%;
}

.github-button:hover {
  color: #000;
  text-decoration: none;
}

@media (max-width: 768px) {
  .projects {
    padding: 4rem 0;
  }

  .projects-description {
    font-size: 1.1rem;
  }

  .projects-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .project-card {
    max-width: 400px;
    margin: 0 auto;
  }

  .project-content {
    padding: 1.5rem;
  }

  .project-title {
    font-size: 1.3rem;
  }

  .project-description {
    font-size: 0.9rem;
  }

  .project-icon {
    font-size: 3rem;
  }

  .project-links {
    gap: 0.8rem;
  }

  .project-link {
    width: 45px;
    height: 45px;
    font-size: 1rem;
  }

  .projects-cta {
    padding: 2rem;
  }

  .cta-text {
    font-size: 1.1rem;
  }

  .github-button {
    font-size: 1rem;
    padding: 0.8rem 1.5rem;
  }
}

@media (max-width: 480px) {
  .project-card {
    max-width: 100%;
  }

  .project-content {
    padding: 1rem;
  }

  .project-title {
    font-size: 1.2rem;
  }

  .project-description {
    font-size: 0.85rem;
  }

  .project-icon {
    font-size: 2.5rem;
  }

  .project-link {
    width: 40px;
    height: 40px;
    font-size: 0.9rem;
  }

  .technology-tag {
    font-size: 0.7rem;
    padding: 0.2rem 0.6rem;
  }

  .projects-cta {
    padding: 1.5rem;
  }

  .cta-text {
    font-size: 1rem;
  }

  .github-button {
    font-size: 0.9rem;
    padding: 0.7rem 1.2rem;
  }
} 